.App {
  text-align: center;
    font-family: Monospaced, sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    -ms-flex: 0 1 40px;
        flex: 0 1 40px;
    background-color: lightblue;
    border-top-style: solid;
    border-top-color: paleturquoise;
}

.navWrapper {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: 100%;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    -ms-flex-align: stretch;
        align-items: stretch;
}

#sidebar {
    min-height: 100%;
    min-width: 300px;
    background-color: cornflowerblue;
    border-right-style: solid;
}

.sidebar-header {
    padding: 20px;
}

#sidebar ul li {
    padding: 10px;
    font-size: 1.3em;
    font-weight: bolder;
    display: block;

}

p {
    text-align: center;
    width: 75%;
    margin: auto;
}

#textBox {
    margin-top: 20px;
}

.table thead th {
    border-style: solid;
    padding: 10px 20px 20px;
}

.table tbody td {
    border-style: solid;
}


